@import url('https://fonts.googleapis.com/css?family=Roboto:300,400,400i,500,700&subset=cyrillic');
@import url('https://fonts.googleapis.com/css2?family=Lato:wght@900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Lato:wght@600&display=swap');
.content {
 	width: 61%;
 	display: flex;
 	flex-direction: column;
 	margin-top:165px;
 	margin-bottom: auto;
 	margin-left: auto;
 	margin-right: auto;
 	&__title {
 		display: inline-block;
 		font-family: Lato;
		font-style: normal;
		font-weight: 900;
		font-size: 56px;
		line-height: 66px;
		/* or 118% */
		color: #FFFFFF;
		margin:0;
 	}
 	&__subtitle {
 		display: inline-block;
 		font-family: Roboto;
		font-style: normal;
		font-weight: normal;
		font-size: 16px;
		line-height: 30px;
		/* or 187% */
		color: #E1E1E1;
		margin-top:40px;
		margin-bottom: 0;
 		}
		&__row{
			display: flex;
			margin-top:40px;
			margin-bottom: auto;
			//margin-left: 0;
			margin-right: auto;
 		}
 		&__row-button {
 			margin: auto;
 			margin-right: 25px;
 			padding-left: 52px;
 			padding-right: 53px;
 			padding-top: 16px;
 			padding-bottom: 15px;
 			border: none;
  		cursor: pointer;
		  font-family: Roboto;
			font-style: normal;
			font-weight: normal;
			font-size: 16px;
			line-height: 30px;
			color: #FFFFFF;
			background: #34B2FF;
			border-radius: 28px;
			display: flex;
			float:left;
 		}
 		&__row-img {
 			display: flex;
 			float:left;
 		}
 		&__row-text {
 			display: flex;
 			margin-left: 16px;
 			float:left;
 			font-family: Roboto;
			font-style: normal;
			font-weight: 500;
			font-size: 16px;
			line-height: 30px;
			/* identical to box height, or 187% */
			color: #FFFFFF;
 		}
}

 @media(max-width: 500px) {
 	.content {
 		width: 75%;
 		margin-top: 30px;
 		&__row {
 			width: 100%;
 			justify-content: space-between;
 		}
 		&__row-button {
 			margin: auto;
 			margin-left: 0;
 		}
 		&__row-img {
 			margin: auto;
 		}
 		&__row-text {
 			margin: auto;
 			margin-top: 10px;
 		}
 		&__watch {
	    margin-left: 10px;
	    display: flex;
	    flex-direction: column;
 		}
 		&__title {
 			font-size: 32px;
 		}
 	}
 }