@import url('https://fonts.googleapis.com/css?family=Roboto:300,400,400i,500,700&subset=cyrillic');
@import url('https://fonts.googleapis.com/css2?family=Lato:wght@900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Lato:wght@600&display=swap');

.subscribe {
	font-size: 1em;
	position: relative;
	margin:auto;
	width:61%;
	display:flex;
	flex-wrap: wrap;
	justify-content: center;
	height:289px;
	z-index: 2;
	&__bg {
		margin:auto;
		background-color:#FFFFFF;
		height:319px;
		padding:15px;
		width:100%;
		box-shadow: 0px 10px 30px rgba(0, 0, 0, 0.08);
		border-radius: 10px;
	}
	&__content{
		background-color:#EBF7FF; 
		height:100%;
		border-radius: 10px;
		display: flex;
		flex-wrap: wrap;
		justify-content:center;
	}
	&__column {
		display:flex;
		flex-direction: column;
		margin:auto;
	//	margin-top:74px;
			width:401px;
	//	margin-bottom: 74px;
	}

	&__column-title {
		font-family: Lato;
		font-style: normal;
		font-weight: bold;
		font-size: 2.25em;
		line-height: 54px;
		/* identical to box height, or 150% */
		color: #171614;
		margin-bottom: 27px;
	}

	&__column-text {
		font-family: Roboto;
		font-style: normal;
		font-weight: normal;
		//font-size: 16px;
		line-height: 30px;
		/* or 187% */
		color: #666666;
	}

	&__content-input {
		margin:auto;
		border:none;
		background: #FFFFFF;
		box-shadow: 0px 6px 20px rgba(0, 0, 0, 0.04);
		border-radius: 5px 0px 0px 5px;
		text-decoration: none;

		width: 337px;
		height: 76px;
		padding-left: 28px;


		font-family: Roboto;
		font-style: normal;
		font-weight: 300;
		font-size: 16px;
		line-height: 30px;
		/* identical to box height, or 187% */
		color: #777777;
		//margin-right: 0;
	}
	&__content-button {
		position: relative;
		//background-image: url("../img/send.svg");
		//background-repeat: no-repeat;
		margin:auto;
		//height: 100%;

		border:none;
		text-decoration: none;
		margin-left: 0;
		padding-top: 25px;
		padding-bottom: 25px;
		padding-left: 28px;
		padding-right: 27px;
	//	margin: 0;

	background: #34B2FF;
	border-radius: 0px 5px 5px 0px;
	}
	&__content-button-img {
		margin: auto;
		height: 26px;
		width: 26px;
	}
	&__pole {
		display: flex;
		margin:auto;
	}
}

@media (max-width: 800px) {
	.subscribe {
		&__content-input {
			width: 100%;
		}
  	&__pole {
			margin-top:10%;
		}
		&__bg {
			height: auto;
			//padding-bottom: 20%;
		}
		&__content {
			height: auto;
			padding-bottom: 10%;
		}
		&__column {
			text-align: center;
			margin-top: 15px;
		}
	}

}

@media (max-width: 769px) {
	.subscribe {
		width: 100%;
		&__content-input {
			width: 337px;
		}
		&__column {
			width: auto;
		}
	}
}

@media (max-width: 500px) {
	.subscribe__content-input {
			width: auto;
		}
	.subscribe__column-title {
			font-size: 27px;
		}
}
