@import url('https://fonts.googleapis.com/css?family=Roboto:300,400,400i,500,700&subset=cyrillic');
@import url('https://fonts.googleapis.com/css2?family=Lato:wght@900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Lato:wght@600&display=swap');
.banner {
	background-image: url("../img/bg.svg");
	background-repeat: no-repeat;
	margin: 0;
	min-height: 939.32px;	
	
}
body {
margin:0;
box-sizing: border-box;
 }

header {
		position: relative;
		width:61%;
		overflow: hidden;
		margin: auto; 
		padding-top:40px;
		padding-bottom: 0;
		display: flex;
}

.logo {
 	display: flex;
 	align-items: center;
 	&__Dace {
 		 	font-family: Roboto;
 		 	font-style: normal;
			font-weight: 500;
			font-size: 30px;
			line-height: 35px;
			color: #FFFFFF;
			display: inline-block;
			margin: 0;
 		}
 		&__img {
 			margin-right: 8.53px;
 			height: 38px;
 		}
 }

 .menu {
 	display: flex;
 	margin:auto;
 	width: 493px;
 	align-items: center;
 	a {
 		margin-left: 0;
 		margin-right: auto;
 		text-decoration: none;
 		font-family: Roboto;
		font-style: normal;
		font-weight: normal;
		font-size: 16px;
		line-height: 30px;
		color: #FFFFFF;
		&:last-child {
			margin-right:0;
		}
 	}
 }

 .quote {
 	padding-left: 35px;
 	padding-right: 35px;
 	padding-top: 11px;
 	padding-bottom: 11px;
  cursor: pointer;
  font-family: Roboto;
	font-style: normal;
	font-weight: normal;
	font-size: 16px;
	line-height: 30px;
	color: #FFFFFF;
	background: #34B2FF;
	border:none;
	border-radius: 28px;
	display: flex;
	margin-right: 0;
	width: 147.48px;
 }
.menu_mobile {
 	display: none;
 	width: 90%;
 	margin: auto;
 	margin-top: 30px;
 	box-shadow: 0px 10px 26px rgba(0, 0, 0, 0.04);
	border-radius: 5px;
 	&__content {
 		margin: auto;
 		display: flex;
 		flex-direction: column;
 		padding-bottom: 10px;
 	}
 	&__content-item {
 		color: #34B2FF;
 		text-align: center;
 		margin-bottom: 10px;
		border: 3px solid #34B2FF;
		box-sizing: border-box;
		border-radius: 25px;
		margin: auto;
 		text-decoration: none;
 		font-family: Roboto;
		font-style: normal;
		font-weight: 500;
		font-size: 24px;
		padding:auto;
		padding-left: 10px;
		padding-right: 10px;
		width: 100%;
		margin-top: 10px;
 	}
 	&__content-item:hover {
 			background: #34B2FF;
 			color: #FFFFFF;
 	}
 }

 @media (max-width: 1280px) {
 	.menu {
 		a {
 			&:last-child {
 			margin-right:auto;
 			}
 			&:first-child {
 				margin-left: auto;
 			}
 		}
 	}
 }

 @media (max-width: 1000px) {
 	.menu {
 		display: none;
 	}
 	.menu_mobile {
 		display: flex;
 	}
 	header {
 		justify-content: space-between;	
 	}
 }

 @media(max-width: 500px) {
 	header {
 		width: 95%;
 	}
 	.banner {
 		text-align: center;
 	}
 }


 