@import url('https://fonts.googleapis.com/css?family=Roboto:300,400,400i,500,700&subset=cyrillic');
@import url('https://fonts.googleapis.com/css2?family=Lato:wght@900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Lato:wght@600&display=swap');

.__footer {
	margin:0;
	background-image: url("../img/bg_footer.svg");
	background-size: cover;
	margin-top:-163px;
	min-height: 830.32px;
}

.footer {
	display: flex;
	flex-direction: column;
	margin:auto;
	&__row {
		display: flex;
		margin:auto;
		margin-top:373px;
		width:61%;
		flex-wrap: wrap;
		justify-content: center;
	}
	&__about {
		margin:auto;
		margin-top: 0;
		display: flex;
		flex-direction: column;
		width: 300px;
		&__social {
			display: flex;
			flex-direction: row;
			margin:0;
			margin-top: 12px;
		}
		&__social-item {
			margin:0;
			height: 44px;
			width: 44px;
			margin-right: 15px;
			align-items: center;
			float: left;
			&:first-child {
				margin-left: -30px;
			}
		}
	}
	&__about-text{
		font-family: Roboto;
		font-style: normal;
		font-weight: normal;
		font-size: 16px;
		line-height: 30px;
		/* or 187% */
		color: #F5F5F5;	
		margin:auto;
		margin-top: 26px;
	}

	&__links {
		margin:auto;
		margin-top: 0;

		&__nav {
			margin-left: 0;
			padding-left: 0;
		}
		&__nav-item {
			list-style-type: none;
		}
		&__nav-item-a {
			margin: auto;
		text-decoration:none;
		color: #FFFFFF;
		font-family: Roboto;
		font-style: normal;
		font-weight: normal;
		font-size: 16px;
		line-height: 40px;
		}
	}
	&__links-title {
		margin:auto;
		font-family: Lato;
		font-style: normal;
		font-weight: 600;
		font-size: 24px;
		line-height: 30px;
		/* identical to box height, or 125% */
		color: #FFFFFF;
		margin-bottom: 38px;
	}
	
	&__copyright{
		display: flex;
		margin:auto;
		width: 61%;
		margin-top: 139px;
		justify-content: space-between;
	}
	&__copyright-item {
		display: flex;
		margin:0;
		margin-left: 0;
		font-family: Roboto;
		font-style: normal;
		font-weight: 300;
		font-size: 16px;
		line-height: 30px;
		/* identical to box height, or 187% */
		color: #FFFFFF;
	}
}


@media (max-width: 700px) {
	.footer  {
		text-align: center;
		&__links {
			margin-left: 10px;
		}
		&__links-title {
			margin: auto;
		}
		&__about {
			margin-bottom: 50px;
			&__social {
				justify-content: center;
			}
		}
		&__copyright{
			width: 95%;
		}
	}
	.footer__about .logo {
			&__img
			{
				margin: auto;
				margin-right: 8.53px;
				}
			&__Dace {
				margin: auto;
				margin-left: 0;
			}
	}

}

@media(max-width: 320px) {
	.footer__links {
		margin:auto;
	}
}