@import url('https://fonts.googleapis.com/css?family=Roboto:300,400,400i,500,700&subset=cyrillic');
@import url('https://fonts.googleapis.com/css2?family=Lato:wght@900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Lato:wght@600&display=swap');

.about {
	display: flex;
	width: 61%;
	margin:auto;
	justify-content: center;
	flex-wrap: wrap;
	margin-bottom: 235px;
	&__img	{
		margin:auto;
		width: 470px;
		height: 571px;
		overflow: hidden;
	}
	&__content {
		display: flex;
		flex-direction: column;
		font-family: Roboto;
		font-style: normal;
		font-weight: normal;
		font-size: 20px;
		line-height: 30px;
		/* identical to box height, or 150% */
		margin:auto;
		width:569px;
	}

	&__head {
		display: inline-flex;
		color: #34B2FF;
		margin:auto;
		margin-left: 0;
	}
	&__title {
		font-weight: bold;
		font-size: 46px;
		line-height: 54px;
		color: #171614;	
		margin:auto;
		margin-top:10px;
		margin-left: 0;
	}
	&__text {
		margin: auto;
		margin-top:30px;
		font-size: 16px;
		color: #666666;
		margin-bottom: 0;
	}
	&__list {
		list-style-image: url("../img/Vector.svg");
		padding:0;
		margin-left: 30px;
		margin-top:38px;
		margin-bottom: 0px;
	}
	&__list-item {
		font-size: 16px;
		color: #444444;
		margin-bottom: 20px;
		padding-left:23px;
	}
	&__button {
		//margin:auto;
		border:none;
		border: 2px solid #34B2FF;
		box-sizing: border-box;
		border-radius: 25px;
		outline: none;
  	cursor: pointer;
  	padding-left:38px; 
  	padding-right:37px;
  	padding-top:11px;
  	padding-bottom: 11px;
  	background-color: #34B2FF;
  	font-family: Roboto;
		font-style: normal;
		font-weight: 500;
		font-size: 16px;
		line-height: 30px;
		/* identical to box height, or 187% */
		text-align: center;
		color: #FFFFFF;
		margin-top: 48px;
		margin-left: 0;
	}
}

@media (max-width:1703px) {
	.about {
		&__content {
		margin-top: 100px;
		}
		&__head {
			margin: auto;
		}
		&__button {
			margin: auto;
		}
		&__title {
			text-align: center;
		}
		&__text {
			text-align: center;
		}
	}
}

@media (max-width: 500px) {
	.about {
		width: 90%;
	}
	.about__img {
		width: auto;
		margin-left: 10px;
		margin-right: 10px;
		background-size: cover;
		overflow: hidden;
		height: auto;
	}
	.about__title {
		font-size: 24px;
		text-align: center;
		margin-left: auto;
	}
}