@import url('https://fonts.googleapis.com/css?family=Roboto:300,400,400i,500,700&subset=cyrillic');
@import url('https://fonts.googleapis.com/css2?family=Lato:wght@900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Lato:wght@600&display=swap');

.features {
	
	display: flex;
	flex-wrap: wrap;
	margin: auto;
	margin-top: 97.68px;
	margin-bottom: 174px;
	width:61%;
	justify-content: center;
	&__item {
	margin:auto;
	display: flex;
	flex-direction: column;
	background: #FFFFFF;
	box-shadow: 0px 10px 26px rgba(0, 0, 0, 0.04);
	border-radius: 5px;
	width: 370px;
	height:525px;
	//padding: 10px;
	padding-bottom: 0;
	margin-bottom: 25px;


	&:hover {
			box-shadow: 0px 10px 30px rgba(52, 178, 255, 0.16);
		}
	}



	&__item-img {
		height: 190px;
		width: 190px;
		margin: auto;
		margin-top: 60px;
	}

	&__item-title {
		margin:auto;
		font-family: Lato;
		font-style: normal;
		font-weight: 600;
		font-size: 24px;
		line-height: 30px;
		/* identical to box height, or 125% */
		text-align: center;
		color: #171614;
	}

		&__item-text {
		padding-top: 30px;
		margin:auto;
		font-family: Roboto;
		font-style: normal;
		font-weight: normal;
		font-size: 16px;
		line-height: 30px;
		/* or 187% */
		text-align: center;
		color: #666666;
		margin-left: 38px;
		margin-right: 37px;
	}

		&__item-button {
		margin:auto;
		border:none;
		border: 2px solid #34B2FF;
		box-sizing: border-box;
		border-radius: 25px;
		outline: none;
  		cursor: pointer;
  		padding-left:41px; 
  		padding-right:40px;
  		padding-top:10px;
  		padding-bottom: 10px;
  		background-color: #FFFFFF;
  		font-family: Roboto;
		font-style: normal;
		font-weight: 500;
		font-size: 16px;
		line-height: 30px;
		/* identical to box height, or 187% */
		text-align: center;
		color: #34B2FF;
		margin-top: 30px;
		margin-bottom: 60px;

		&:hover {
			background-color:#34B2FF;
			color:#FFFFFF;

		}
	}
}

@media (max-width: 500px){
	.features {
		width:99%;
		&__item {
			width: 95%;
		}
	}
}